body{
  background-color:  #0B2434;
  padding: 10px
}
.iconcamera{
  width: 75px;
  height :65px;
  margin-left: 100px;
  border-radius: 10px;
  margin-bottom: 10px
}
.qrimage{
  width: 75px;
  height :75px;
  justify-content: center;
  text-align: center;
}
.iconcamera:active {
  background-color: #222;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}
.footer{
  color: #F00;
  text-align: center;
}

.footer2{
  color: #00F;
  text-align: center;
}

.prodimg{
  width: 100px;
  height :100px;
  font-size: 12px;
  flex:0 0 auto;
  display: flex;
flex-direction: column;
padding: 1px;
position: relative;
  border-radius: 9px;
}
.prodsgroup{
  display: inline-grid;
grid-template-columns: auto auto auto auto auto;
grid-gap: 15px;
align-items: center;
background-color: #FDFDFD;
border-radius: 10px;
width: 100%;
}
.qrcode{
  display: inline-grid;
grid-template-columns: auto auto ;
align-items: center;
justify-content: center;
margin-left: 250px;
}
.prodsgroupwith{
  display: inline-grid;
grid-template-columns: auto auto auto auto auto auto auto auto;
grid-gap: 15px;
align-items: center;
background-color: #FDFDFD;
border-radius: 10px;
width: 100%;
}

.prodsgroup2{
  display: inline-grid;
grid-template-columns: auto auto auto auto auto auto auto;
grid-gap: 15px;
align-items: center;
}

@media print {
  .save-button{
    display: none;
  }

  .conent{
    width: 100%;
  }
  h1{
    break-before: always;
  }
  table,img,svg{
    break-inside: avoid;
  }

}


.header{
  display: inline-grid;
grid-template-columns: auto auto auto auto auto auto;
grid-gap: 15px;
align-items: center;
background-color: #FDFDFD;
border-radius: 10px;
width: 100%;
}
.total{
  padding: 10px;
  margin-left: auto;
  width: 50px;
}
.main-input{
  width: 50px;
  flex-direction: column;
}
.main-sym{
  padding: 5px;
  border-radius: 5px;
  width: 20%;
  flex-direction: column;
}
.main-button{
  border-radius: 5px;
background-color:  #AAA;
width: 100px;

}

.button {
  display: inline-block;
  padding: 15px 25px;
  font-size: 15px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: rgb(48, 87, 48);
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
}

.button:hover {background-color: #16da37}

.button:active {
  background-color: #16da37;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

